body {
	font-family: 'Roboto Condensed', sans-serif;
}

/* GLOBAL*/
html,
body {
	font: 16px/1.5 Arial, Helvetica, sans-serif;
	margin: 0;
	padding: 0;
	height: 100vh;
	width: 100%;
	/*background-color: #f4f4f4;*/
}

.container {
	display: block;
	width: 80%;
	margin: 0 10%;
	padding: 0;
	overflow: hidden;
}

#sign_in_header {
	width: 100%;
	height: 70px;
	background-color: red;
}

#logo_header {
	border: solid 2px black;
	display: inline-block;
	/*float: left;*/
	margin-top: 5px;
	margin-bottom: 5px;
}

#login {
	float: right;
	line-height: 60px;
	/*border:solid 1px black;*/
}

#username_login {
	display: inline-block;
	/*border:solid 1px black;*/
}

#password_login {
	display: inline-block;
	/*border:solid 1px black;*/
}

/* Media Rules*/
@media only screen and(max-width:900px) {}

.right {
    position: fixed;
    top: 0;
    right: 0;
    width: 50%;
    height: 100%;
    background-color: white; 
}

#left{
	position: fixed;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    background-image: url(/static/media/Purple-Abstract.8dadc391.jpg);
}

#logo_header{
	font-size: 70px;
	margin-left: 30%;
	margin-top: 50%;
	font-family: 'Roboto Condensed', sans-serif;
}

#login{
	margin-left: 30%;
	font-size: 30px;
}

#password_login{
	margin-top: 5%;
}

button{
	border-radius: 20px;
}

input{
	border-radius: 10px;
	border-width: 1px;
	border-color: black;
	background-color: white;
	height: 25px;
} 

